import { FC, useEffect, useRef, useState } from "react";
import {
  Box,
  Center,
  Image,
  Heading,
  Text,
  HStack,
  Progress,
} from "@chakra-ui/react";

import { PreviousIcon, NextIcon } from "utils/Icons";

interface CardItem {
  id: number;
  title: string;
  description: string;
  image: string;
}

const cardData: CardItem[] = [

  {
    id: 1,
    title: "BIASsist : Bias Assistive Tool",
    description: "An LLM-based approach to mitigate bias experssion",
    image: "/assets/publication_img/9Publi.png",
  },
  {
    id: 2,
    title: "Conversational Agent for Dementia",
    description: "Conversational agents for personalized reminiscence.",
    image: "/assets/publication_img/7Publi.png",
  },
  {
    id: 3,
    title: "Gaze Identification",
    description: "Authentication using pre-attentive gaze patterns.",
    image: "/assets/publication_img/8Publi.png",
  },
  {
    id: 4,
    title: "Music for the Deaf",
    description: "Transforming sound into vibrotactile feedback.",
    image: "/assets/publication_img/6Publi.png",
  },
  {
    id: 5,
    title: "Conversational Agent in Museum",
    description: "Designing reenacted CAs to enhance museum experience",
    image: "/assets/publication_img/2Publi.png",
  }
];

export const Research: FC = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [progress, setProgress] = useState(10);
  const [isPaused, setIsPaused] = useState(false);

  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const duration = 5000;
  const tick = 20;
  const steps = duration / tick;

  useEffect(() => {
    if (!isPaused) {
      intervalRef.current = setInterval(() => {
        setProgress((prev) => {
          if (prev >= 100) {
            goToNext();
            return 10;
          }
          return prev + 100 / steps;
        });
      }, tick);
    }

    return () => {
      if (intervalRef.current) clearInterval(intervalRef.current);
    };
  }, [currentIndex, isPaused]);

  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev === 0 ? cardData.length - 1 : prev - 1));
    setProgress(10);
  };

  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % cardData.length);
    setProgress(10);
  };

  const handleSelect = (index: number) => {
    setCurrentIndex(index);
    setProgress(10);
  };

  return (
    <Box
      w="100%"
      maxW="800px"
      mx="auto"
      textAlign="center"
      // onMouseEnter={() => setIsPaused(true)}
      // onMouseLeave={() => setIsPaused(false)}
    >
      {/* Ïπ¥Îìú Ïä¨ÎùºÏù¥Îìú ÏòÅÏó≠ */}
      <Box position="relative" height="420px" overflow="hidden">
        {/* Ïπ¥ÎìúÎì§ */}
        {cardData.map((card, index) => {
          const isCurrent = index === currentIndex;
          const isLeft = index === (currentIndex - 1 + cardData.length) % cardData.length;
          const isRight = index === (currentIndex + 1) % cardData.length;

          const display = isCurrent || isLeft || isRight ? "block" : "none";
          const scale = isCurrent ? 1 : 0.9;
          const zIndex = isCurrent ? 2 : 1;
          const opacity = isCurrent ? 1 : 0.5;
          const offsetX = isLeft ? "-60%" : isRight ? "60%" : "0";

          return (
            <Box
              key={card.id}
              position="absolute"
              top="0"
              left="50%"
              transform={`translateX(-50%) translateX(${offsetX}) scale(${scale})`}
              transition="all 0.5s ease-in-out"
              zIndex={zIndex}
              opacity={opacity}
              display={display}
              w="100%"
              maxW="600px"
            >
              <Box
                borderWidth="1px"
                borderRadius="lg"
                overflow="hidden"
                boxShadow="lg"
                bg="white"
                p={4}
              >
                <Center h="200px">
                  <Image
                    src={card.image}
                    alt={card.title}
                    maxH="100%"
                    maxW="100%"
                    objectFit="contain"
                  />
                </Center>
                <Heading color="black" fontSize="xl" mt={4}>
                  {card.title}
                </Heading>
                <Text mt={2} color="gray.600">
                  {card.description}
                </Text>
              </Box>
            </Box>
          );
        })}

        {/* ÏôºÏ™Ω Î≤ÑÌäº */}
        <Box
          as="button"
          onClick={goToPrevious}
          position="absolute"
          left="0"
          top="40%"
          transform="translateY(-50%)"
          zIndex={3}
          w="50px"
          h="80px"
          display="flex"
          alignItems="center"
          justifyContent="center"
          
          borderRadius="full"
          _hover={{ bg: "gray.100" }}
        >
          <PreviousIcon color="black" style={{ width: "40px", height: "40px" }} />
        </Box>

        {/* Ïò§Î•∏Ï™Ω Î≤ÑÌäº */}
        <Box
          as="button"
          onClick={goToNext}
          position="absolute"
          right="0"
          top="40%"
          transform="translateY(-50%)"
          zIndex={3}
          w="50px"
          h="80px"
          display="flex"
          alignItems="center"
          justifyContent="center"
          borderRadius="full"
          _hover={{ bg: "gray.100" }}
        >
          <NextIcon color="black" style={{ width: "40", height: "40" }} />
        </Box>
      </Box>
      

      {/* ÌïòÎã® ÏßÑÌñâ Î∞î Ïù∏ÎîîÏºÄÏù¥ÌÑ∞ */}
      <HStack mt="-50" spacing={2}  justify="center">

        {cardData.map((_, index) => (
          <Box
            key={index}
            w="80px"
            cursor="pointer"
            onClick={() => handleSelect(index)}
          >
            <Progress
              size="xs"
              colorScheme={index === currentIndex ? "blue" : "gray"}
              value={
                index === currentIndex
                  ? progress
                  : index < currentIndex
                  ? 100
                  : 0
              }
              borderRadius="full"
              transition="all 0.2s"
            />
          </Box>
        ))}
      </HStack>
    </Box>
  );
};
